:root, [data-theme="light"] {
  // main
  @include defineColorHSL(--color-primary-darker, 239, 29%, 34%);
  @include defineColorHSL(--color-primary-dark, 239, 29%, 44%);
  @include defineColorHSL(--color-primary, 239, 29%, 54%);
  @include defineColorHSL(--color-primary-light, 239, 29%, 64%);
  @include defineColorHSL(--color-primary-lighter, 239, 29%, 74%);

  @include defineColorHSL(--color-accent-darker, 342, 89%, 28%);
  @include defineColorHSL(--color-accent-dark, 342, 89%, 38%);
  @include defineColorHSL(--color-accent, 342, 89%, 48%);
  @include defineColorHSL(--color-accent-light, 342, 89%, 58%);
  @include defineColorHSL(--color-accent-lighter, 342, 89%, 68%);

  @include defineColorHSL(--color-black, 0, 0%, 0%);
  @include defineColorHSL(--color-white, 0, 0%, 100%);

  // feedback
  @include defineColorHSL(--color-warning-darker, 37, 100%, 50%);
  @include defineColorHSL(--color-warning-dark, 37, 100%, 60%);
  @include defineColorHSL(--color-warning, 37, 100%, 70%);
  @include defineColorHSL(--color-warning-light, 37, 100%, 80%);
  @include defineColorHSL(--color-warning-lighter, 37, 100%, 90%);

  @include defineColorHSL(--color-success-darker, 164, 100%, 19%);
  @include defineColorHSL(--color-success-dark, 164, 100%, 29%);
  @include defineColorHSL(--color-success, 164, 100%, 39%);
  @include defineColorHSL(--color-success-light, 164, 100%, 49%);
  @include defineColorHSL(--color-success-lighter, 164, 100%, 59%);

  @include defineColorHSL(--color-error-darker, 1, 67%, 52%);
  @include defineColorHSL(--color-error-dark, 1, 67%, 62%);
  @include defineColorHSL(--color-error, 1, 67%, 72%);
  @include defineColorHSL(--color-error-light, 1, 67%, 82%);
  @include defineColorHSL(--color-error-lighter, 1, 67%, 92%);

  // background
  @include defineColorHSL(--color-bg-darker, 216, 9%, 89%);
  @include defineColorHSL(--color-bg-dark, 0, 0%, 95%);
  @include defineColorHSL(--color-bg, 0, 0%, 100%);
  @include defineColorHSL(--color-bg-light, 0, 0%, 100%);
  @include defineColorHSL(--color-bg-lighter, 240, 2%, 100%);

  // color contrasts
  @include defineColorHSL(--color-contrast-lower, 0, 0%, 95%);
  @include defineColorHSL(--color-contrast-low, 0, 0%, 90%);
  @include defineColorHSL(--color-contrast-medium, 225, 2%, 52%);
  @include defineColorHSL(--color-contrast-high, 230, 4%, 30%);
  @include defineColorHSL(--color-contrast-higher, 223, 8%, 17%);
}

body, [data-theme="light"] {
  // font rendering
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@supports(--css: variables) {
  [data-theme="dark"] {
    // main
    @include defineColorHSL(--color-primary-darker, 250, 93%, 57%);
    @include defineColorHSL(--color-primary-dark, 250, 93%, 61%);
    @include defineColorHSL(--color-primary, 250, 93%, 65%);
    @include defineColorHSL(--color-primary-light, 250, 93%, 69%);
    @include defineColorHSL(--color-primary-lighter, 250, 93%, 72%);

    @include defineColorHSL(--color-accent-darker, 342, 92%, 41%);
    @include defineColorHSL(--color-accent-dark, 342, 92%, 47%);
    @include defineColorHSL(--color-accent, 342, 92%, 54%);
    @include defineColorHSL(--color-accent-light, 342, 92%, 60%);
    @include defineColorHSL(--color-accent-lighter, 342, 92%, 65%);

    @include defineColorHSL(--color-black, 204, 28%, 7%);
    @include defineColorHSL(--color-white, 0, 0%, 100%);

    // feedback
    @include defineColorHSL(--color-warning-darker, 46, 100%, 47%);
    @include defineColorHSL(--color-warning-dark, 46, 100%, 50%);
    @include defineColorHSL(--color-warning, 46, 100%, 61%);
    @include defineColorHSL(--color-warning-light, 46, 100%, 71%);
    @include defineColorHSL(--color-warning-lighter, 46, 100%, 80%);

    @include defineColorHSL(--color-success-darker, 122, 50%, 47%);
    @include defineColorHSL(--color-success-dark, 122, 50%, 52%);
    @include defineColorHSL(--color-success, 122, 50%, 60%);
    @include defineColorHSL(--color-success-light, 122, 50%, 69%);
    @include defineColorHSL(--color-success-lighter, 122, 50%, 76%);

    @include defineColorHSL(--color-error-darker, 342, 92%, 41%);
    @include defineColorHSL(--color-error-dark, 342, 92%, 47%);
    @include defineColorHSL(--color-error, 342, 92%, 54%);
    @include defineColorHSL(--color-error-light, 342, 92%, 60%);
    @include defineColorHSL(--color-error-lighter, 342, 92%, 65%);

    // background
    @include defineColorHSL(--color-bg-darker, 210, 3%, 44%);
    @include defineColorHSL(--color-bg-dark, 0, 0%, 5%);
    @include defineColorHSL(--color-bg, 0, 0%, 9%);
    @include defineColorHSL(--color-bg-light, 0, 0%, 13%);
    @include defineColorHSL(--color-bg-lighter, 240, 1%, 16%);

    // color contrasts
    @include defineColorHSL(--color-contrast-lower, 240, 1%, 20%);
    @include defineColorHSL(--color-contrast-low, 240, 1%, 37%);
    @include defineColorHSL(--color-contrast-medium, 240, 1%, 54%);
    @include defineColorHSL(--color-contrast-high, 240, 4%, 81%);
    @include defineColorHSL(--color-contrast-higher, 240, 100%, 99%);

    // font rendering
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}