.ms-404-page {
  height: calc(90vh - var(--main-header-height-md));
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  h2 {
    margin-bottom: 15px;
  }
  .ms-404--img {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      max-height: 50vh;
    }
    
  }
}

@media only screen and (max-width: 822px) {
  .ms-404-page {
    text-align: center;
    a {
      margin-bottom: 10vh;
    }
    .ms-404--content {
      margin-top: 2vh;
      text-align: center;
      h1 {
        font-size: calc(2 * var(--text-xxl));
        margin-bottom: 30px;
      }
      p {
        max-width: 100%;
      }
    }
  }
}

@media only screen and (max-width: 512px) {
  #cookie-toast-wrapper{
    bottom: 10px;
    left: 10px;
    right: 10px;
    width: auto;
    transform: translate(0) !important;
    #cookie-inner-wrapper {
      grid-template-columns: auto;
      text-align: center;
    }
  }  
}

