:root {
  --btn-font-size: 1em;
  --btn-padding-x: var(--space-sm);
  --btn-padding-y: var(--space-xxs);
  --btn-radius: 0;
}

button:focus {
  outline: none;
}
// Input Submit {
input[type=submit]:not(.wpcf7-form-control) {
  cursor: pointer;
  border: none;
  color: var(--color-white);
  margin-left: 8px;
  padding: 0 1em;
}
.entry-content input[type=submit]{
  background-color: var(--color-primary);
}
// var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
.btn { // basic button style
  font-weight: 400;
  // outline: none;
  display: inline-flex;
  position: relative;
  white-space: nowrap;
  text-decoration: none;
  padding-top: var(--space-xxs);
  padding-bottom: var(--space-xxs);
  padding-left: var(--space-md);
  padding-right: var(--space-md);
  border-radius: var(--btn-radius);
  font-size: var(--btn-font-size);
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: var(--color-white);
  user-select: none;
  outline: none;
  outline-color: transparent;
  box-shadow: none;
  will-change: transform;
  overflow: hidden;
  backface-visibility: hidden;
  transition: outline-color 250ms var(--ease-out), background-color 250ms var(--ease-out), transform 150ms var(--bounce);

  &:active {
    transform: translateY(2px);
    outline: none;
  }
  &:focus {
    box-shadow: none;
  }

}

// Btn Gutenberg
.wp-block-button {
  margin-bottom: var(--global--spacing-vertical);
}
// themes
.btn--primary { // main button
  height: 42px;
  position: relative;
  background-color: var(--color-primary);
  color: var(--color-white);
  transition: background-color 250ms var(--ease-out), transform 150ms var(--ease-in-out), outline-color 250ms var(--ease-out);
  @include fontSmooth;
}

.btn--subtle {
  background: var(--color-contrast-lower);
  color: var(--color-contrast-higher);
  outline: none;
}

.btn--accent { // used to draw particular attention to the button (e.g. destructive actions)
  background-color: var(--color-accent);
  color: var(--color-white);
  @include fontSmooth;

  &:hover, :focus, :active {
    color: var(--color-white);
    outline-color: var(--color-accent);
  }

}

// feedback
.btn--disabled {
  cursor: not-allowed;
  box-shadow: none;
  opacity: 0.6;
  pointer-events: none;
}

// size
.btn--sm {
  font-size: 0.8em;
}

.btn--ba {
  font-size: 1em;
}

.btn--md {
  font-size: 1.1em;
}

.btn--lg {
  font-size: 1.4em;
}

.btn--full-width {
  display: flex;
  width: 100%;
}

// icons
.btn .icon {
  // icon inherits color of button label
  color: inherit;
  flex-shrink: 0;
}

// icons + text
.btn--icon-text {
  align-items: center;
  
  > *:nth-child(2) {
    margin-left: var(--space-xs);
  }
}

// btn with icon (only)
.btn--icon {
  padding: var(--space-xs);
}

/* -------------------------------- 

File#: _1_btn-states
Title: Buttons states
Descr: Multi-state button elements

-------------------------------- */
.btn-load-more {
  overflow: hidden;

  .text--no-items {
    display: none;
  }
}
.no-works.btn--primary {
  background-color: var(--color-contrast-lower);
  color: var(--color-contrast-medium);
  pointer-events: none;
  box-shadow: none;

  .text--main {
    display: none;
  }
  .ms-btn__text {
    transition: margin-right 250ms cubic-bezier(.645,.045,.355,1);
  }
  .text--no-items {
    display: block;
  }

  &::hover {
    pointer-events: none;
  }
}
.btn .btn__content-b {
  position: absolute;
  box-shadow: none;
  top: -2pt;
  left: calc(100% + 4pt);
  transform: scale(0);
  transition: all 250ms cubic-bezier(.645,.045,.355,1);
}

.btn__content-a, .btn__content-b {
  align-items: center;
}
.btn__content-c {
  top: -1px;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 1;
  position: absolute;
  box-shadow: none !important;
  transform: scale(1);
  transition: all 300ms ease;
}
.btn--state-b {

  .ms-btn__text {
    margin-right: 35px;
  }
  .btn__content-a {
    display: none;
    transition: top 250ms cubic-bezier(.645,.045,.355,1);
  }

  .btn__content-b {
    transform: scale(1);
  }
}

/* preserve button width when switching from state A to state B */
.btn--preserve-width {
  .btn__content-b {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
  }

  &.btn--state-b .btn__content-a {
    display: inline-block; // fallback
    display: inline-flex;
  }
  &.btn--state-b .btn__content-b {
    top: 0;
    transition: top 300ms ease;
  }
    &.btn--state-b .btn__content-c {
    opacity: 0;
    transform: scale(0);
    transition: all 300ms ease;
  }
}
.load-more-icon {
  :first-child {
    stroke: var(--color-primary-light);
  }
  :last-child {
    stroke: var(--color-white);
  }
}

.wpcf7 > form input[type="submit"][disabled],
.wpcf7 > form input[type="submit"][disabled]:hover,
.wpcf7 > form input[type="submit"][disabled]:focus {
    color: var(--color-white);
    background-color: var(--color-contrast-medium) !important;
    transition: all 250ms var(--ease-out);
}

// With Icon
.ms-btn__icon {
  z-index: 4;
}
.ms-btn__icon--left {
  margin-right: 4pt;
}
.ms-btn__icon--right {
  margin-left: 4pt;
}
.ms-btn__text,
.ms-btn__icon {
  transition: all 250ms var(--ease-out);
}
.ms-btn__text:focus,
.ms-btn__text:active,
.ms-btn__icon:focus,
.ms-btn__icon:active,
.ms-btn__icon i:focus,
.ms-btn__icon i:active {
  box-shadow: none;
  user-select: none;
}


/*--------------------------------*/
/* NicsX Effect
/*--------------------------------*/
.ms-btn__text {
  position: relative;
  backface-visibility: hidden;
  .text--main {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    backface-visibility: hidden;
    will-change: transform;
    transition: transform .8s cubic-bezier(.19,1,.22,1) .1s,opacity .4s cubic-bezier(.19,1,.22,1) .1s;
  }
  .text--ghost {
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: 4;
    color: var(--color-white);
    will-change: transform;
    transform: translateY(150%) rotate(25deg);
    transition: transform .8s cubic-bezier(.19,1,.22,1),opacity .4s cubic-bezier(.19,1,.22,1);
  }

}
.btn:hover .text--main {
  transform: translateY(-150%) rotate(-25deg);
  opacity: 0;
  transition-delay: 0s;
}
.btn:hover .text--ghost {
  transform: translateY(0) rotate(0);
  opacity: 1;
  transition-delay: .2s;
}
.btn:after, .btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transition: transform .8s cubic-bezier(.19,1,.22,1);
  transform-origin: bottom;
}
.btn:before {
  background-color: #ABA9BB;
  z-index: 3;
  transition-delay: .07s;
}
.btn:after {
  background-color: #8989be;
  z-index: 2;
  transition-delay: .15s;
}
.ms-btn--ripple {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: bottom;
  background-color: #464555;
  z-index: 3;
  transform: scaleY(0);
  transition: transform .8s cubic-bezier(.19,1,.22,1);
  transition-delay: 0s;
}
// Hover
.btn:hover  {
  color: var(--color-white);
}
.btn:hover:after {
  transition-delay: 0s;
}
.btn:hover:before {
  transition-delay: .08s;
}
.btn:hover:after, .btn:hover:before {
  transform: scaleY(1);
}
.btn:hover .ms-btn__icon {
  color: var(--color-white);
}
.btn:hover .ms-btn--ripple {
  transform: scaleY(1);
  transition-delay: .15s;
}

@media (pointer:coarse){
  .btn:hover {
    &:after, &:before {
      transform: scale(0);
    }
    .ms-btn--ripple {
      transform: scale(0);
    }
    .text--main {
      opacity: 1;
      transform: translateZ(0);
    }
    .text--ghost {
      opacity: 0;
      transform: translateZ(0);
    }
  }
}