// Post Format Video
:root {
    --plyr-color-main: var(--color-primary);
  }

// Post Format Video
.ms-post-media__video {
    margin-bottom: 20pt;
    border-radius: 8pt;
    overflow: hidden;

    .plyr__video-wrapper {
        position: relative;
        
    }
    
    .plyr__poster {
        background-size: cover;
    }

    .plyr__control--overlaid {
        background: var(--color-primary);
        overflow: hidden;

        svg {
            z-index: 3;
        }
    }

}
.media-wrapper--16\:9 {
    .plyr__video-wrapper {
        padding-bottom: calc((9 / 16) * 100%);
    }
}
.media-wrapper--21\:9 {
    .plyr__video-wrapper {
        padding-bottom: calc((9 / 21) * 100%);
    }
}
// Post Format Audio
.ms-post-media__audio {
    margin-top: 8pt;
    margin-bottom: 20pt;
    border-radius: 6pt;
    
    .plyr--audio .plyr__controls {
        border-radius: 6pt;
        background-color: hsla(var(--color-contrast-high-h), var(--color-contrast-high-s), var(--color-contrast-high-l),var(--color-o,.05));
        transition: background-color .6s cubic-bezier(.19,1,.22,1);
    }
}
.ms-single-post .ms-post-media__audio {
    max-width: 800px;
    margin: 8pt auto 0 ;
}
.ms-sp--article .plyr--audio .plyr__controls {
    border-radius: 6pt;
    background-color: hsla(var(--color-contrast-high-h), var(--color-contrast-high-s), var(--color-contrast-high-l),var(--color-o,.05));
    transition: background-color .6s cubic-bezier(.19,1,.22,1);
}

// Post Format Gallery
.post_format-post-format-gallery {

    .ms-post-media__gallery {
        padding-bottom: calc((9 / 16) * 100%);
        margin-bottom: 20pt;
    }

}
.ms-single-post--img img {
    border-radius: 8pt;
}

.ms-single-post--img {
    padding-right: 15px;
    padding-left: 15px;

    .ms-post-media__gallery {
        border-radius: 8pt;
        padding-bottom: calc((9 / 21) * 100%);

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

}

.ms-post-media__gallery {
    overflow: hidden;
    position: relative;
    

    .swiper-wrapper {
        position: absolute;
        top: 0;
        left: 0;
    }

    .ms-sp-btn__prev,
    .ms-sp-btn__next {
        position: absolute;
        width: 46px;
        height: 46px;
        z-index: 9999;
        top: 50%;
        opacity: 0;
        border-radius: 0;
        transform: translateY(-55%) scale(.8);
        background-color: hsla(var(--color-contrast-high-h), var(--color-contrast-high-s), var(--color-contrast-high-l),var(--color-o,.7));
        backdrop-filter: saturate(180%) blur(15px);
        transition: all .6s cubic-bezier(.19,1,.22,1);

        svg {
            stroke: var(--color-white);
            fill: none;
            stroke-linecap:round;
            stroke-linejoin:round;
            stroke-width:2px;
            padding: 10px;
        }
    }
    .ms-sp-btn__next {
        right: 0pt;
        svg {
            transform: rotate(90deg);
        }
            }
    .ms-sp-btn__prev {
        left: 0pt;
        svg {
            transform: rotate(-90deg);
        }
    }
    a {
        display: block;
        height: 100%;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    &:hover {
        .ms-sp-btn__next {
            opacity: 1;
            right: 16pt;
            transform: translateY(-50%) scale(1);
            border-radius: 3pt;
        }
        .ms-sp-btn__prev {
            opacity: 1;
            left: 16pt;
            transform: translateY(-50%) scale(1);
            border-radius: 3pt;
        }
    }
}

// Post Format Quote
// Post Format Link
.ms-post-quote,
.ms-post-link {
    border: solid 3px var(--color-primary);
    padding: 1.75rem;
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 8pt;

    .post-quote__icon {
        display: inline-block;
    }

    .post-content {
        position: relative;
        margin-left: 30px;

        h2 {
            text-transform: uppercase;
            font-weight: 800;
            font-size: 24px;
        }
    }

    .post-footer {
        margin-top: 6pt;
        color: var(--color-contrast-medium);
    }

    h2:hover {
        color: var(--color-contrast-higher) !important;
    }
}

.ms-post-quote {
    position: relative;
    &::before {
        content: '\201C';
        position: absolute;
        font-size: 100px;
        line-height: 1;
        background: var(--color-bg);
        overflow: hidden;
        top: 1.55rem;
        left: -1.1rem;
        z-index: 1;
        height: 50px;
        color: var(--color-primary);
        font-family: sans-serif;
    }

}


.ms-post-link {
    .post-quote__icon {
        position: absolute;
        height: 50px;
        width: 30px;
        display: flex;
        align-items: center;
        background-color: var(--color-bg);
        left: -1rem;
        top: 1.5rem;
    }

    svg {

        path {
            fill: var(--color-primary);
        }
    
    }

}


.ms-single-post--img {

    .ms-post-quote,
    .ms-post-link {
        max-width: 800px;
        margin: 0 auto;
    }

}

// Responsive
@media only screen and (max-width: 1028px) {

    .ms-single-post--img {
        padding-left: 0;
        padding-right: 0;
    }

}

@media only screen and (max-width: 552px) {
    .ms-single-post--img {
        padding-left: calc(var(--bs-gutter-x)/ 2)!important;
        padding-right: calc(var(--bs-gutter-x)/ 2)!important;
    }
}