/* Navigation
--------------------------------------------- */
.main-header__layout {

  .main-header__nav {
    font-size: 1.25em; // controls the font-size of all elements (sm)

    ul {
      list-style: none;
      padding-left: 0;
    }

  }
  .main-header__default .navbar-nav .menu-item a {
    font-size: 17px;
    transition: color 250ms cubic-bezier(.645,.045,.355,1);
    padding-top: calc(var(--space-sm) + 2pt);
    padding-bottom: calc(var(--space-sm) + 2pt);
    cursor: pointer;
  }

  .main-header__default .navbar-nav .menu-item:hover > a {
    color: var(--color-primary);
  }
  .main-header__nav .menu-item {
    position: relative;

    > a {

      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      color: var(--color-contrast-higher);
      text-decoration: none;
      padding: .5em 0;
      padding: var(--space-xs) var(--space-sm);

    }
  }

  .main-header__nav-label { // menu label
    color: var(--color-contrast-medium);
    margin-bottom: var(--space-xs);
  }

  .main-header__nav-item { // nav list item
    margin-bottom: var(--space-xs);
  }

  .main-header__nav-link { // link within list item
    color: var(--color-contrast-high);

    &[aria-current] { // style of selected link
      color: var(--color-primary);
    }
  }

  .main-header__nav-divider { // element used to separate nav items
    height: 1px;
    width: 100%;
    background-color: var(--color-contrast-low);
  }
}

/* Menu Trigger
--------------------------------------------- */
.main-header__nav-trigger { // menu button
  display: none; // hidden if js = disabled
}

/* Menu Icon
--------------------------------------------- */
.main-header__nav-trigger-icon {
  position: relative;
  display: block;
  height: 2px;
  width: 1em;
  background-color: currentColor;
  margin-right: var(--space-xxs);
  transition: 0.25s ease;

  &::before, &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: inherit;
    height: inherit;
    background-color: currentColor;
    transition: 0.2s;
  }

  &::before {
    transform: translateY(-5px);
  }

  &::after {
    transform: translateY(5px);
  }
}

.main-header__nav-trigger[aria-expanded="true"] .main-header__nav-trigger-icon {
  background-color: transparent;

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.js { // if js = enabled
  .main-header, .main-header__layout {
    height: var(--main-header-height);
  }

  .main-header__layout {
    z-index: 99;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-header__nav.ms-nav-drawer {
    position: relative;
    background-color: transparent;
    box-shadow: none;
    top: 0;
    left: 0;
    width: auto;
    padding: 0;
    display: block;
  }
  .main-header__nav--is-visible {
    display: block;
    z-index: 2;
    &::after {
      content: '';
      position: absolute;
      top: -6px;
      right: calc(var(--space-lg) + 7pt);
      height: 20px;
      width: 20px;
      background-color: var(--color-contrast-higher);
      transform: rotate(45deg);
    }
  }

  .main-header__nav-trigger {
    display: inline-flex; // show btn
    z-index: 3;
    background-color: transparent!important;
    box-shadow: none!important;
    color: var(--color-contrast-high);
    outline: none;
  }
}


.main-header__nav .sub-menu {
  margin-left: 15px;
  font-size: var(--text-xs);
  backface-visibility: hidden;
  display: block;

  a {
    color: var(--color-contrast-high);
    backface-visibility: hidden;
    outline: none;
    transition: color .6s cubic-bezier(.405,0,.025,1);
  }

}
.sub-menu.sub-menu---visible {
  display: block;
}

@include breakpoint(md) {
  .main-header, .main-header__layout {
    height: var(--main-header-height-md);
  }
  .main-header__default .navbar-nav {
    display: flex;
    flex-direction: row;
  }
  .main-header__layout {
    padding: 0;
    display: table; // flex fallback
    width: 100%; // flex fallback
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-header__logo {
    display: table-cell; // flex fallback
    vertical-align: middle; // flex fallback
    z-index: 999;
  }

  .main-header__nav {
    float: right; // flex fallback
    margin-top: 0;
    font-size: 0.875em; // controls the font-size of all elements (md)
  }

  .main-header__nav-label { // hide label
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
  }

  .main-header__nav-list {
    display: flex;
    align-items: center;
  }

  .main-header__nav-item {
    display: inline-block; // flex fallback
    margin-bottom: 0;
    margin-left: var(--space-md);
  }

  .main-header__nav-divider {
    height: 1em;
    width: 1px;
  }
  .js {
    .main-header, .main-header__layout {
      height: var(--main-header-height-md);
    }

    .main-header__nav {
      padding: 0;
      background-color: transparent;
      position: static;
      width: auto;
      box-shadow: none;
      display: block;
      z-index: 3;
    }

    .main-header__nav-trigger {
      display: none;
    }
  }

/* Mobile Submenu
--------------------------------------------- */
.main-header__layout {
.main-header__nav .navbar-nav .sub-menu {
    font-size: 14px;
    position: absolute;
    top: 100%;
    width: 220px;
    left: calc(50% - 100px);
    margin: 0;
    background-color: var(--color-contrast-higher);
    z-index: 5;
    z-index: var(--zindex-popover);
    visibility: hidden;
    opacity: 0;
    transform: translateY(10px) perspective(1px);
    backface-visibility: hidden;
    transition: visibility 250ms ease, opacity 250ms ease, transform .55s cubic-bezier(.19,1,.22,1);
    border-radius: var(--radius-lg);
    box-shadow: 0 1px 1px rgba(0,0,0,0.05), 
                0 2px 2px rgba(0,0,0,0.20), 
                0 4px 4px rgba(0,0,0,0.15), 
                0 8px 8px rgba(0,0,0,0.05);

    &::after {
      content: '';
      position: absolute;
      background-color: var(--color-contrast-higher);
      border-radius: var(--radius-md);
      width: 14px;
      height: 14px;
      top: -.3rem;
      left: calc(50% - 14px);
      z-index: -1;
      transform: rotate(45deg);
    }

    li {
      width: 100%;
      margin-left: 0;
      border-radius: .25rem;

      > a {
        line-height: 1.4;
        font-size: 15px;
        color: var(--color-contrast-lower) !important;
        padding: 6pt 8pt;
        margin-left: 8pt;
        margin-right: 8pt;
        cursor: pointer;
        border-radius: 4pt;
        transition: background-color 150ms ease;

        &:hover {
          opacity: 1;
          background-color: var(--color-contrast-high);
        }

      }

      &:first-child > a {
        margin-top: 6pt;
      }
      &:last-child > a {
        margin-bottom: 6pt;
      }

    }

    .sub-menu {
      top: -6pt;
      left: -100%;

      &::after {
        content: none;
      }

    }
}

  .navbar-nav .menu-item-has-children:hover > .sub-menu {
    visibility: visible;
    transform: translateZ(0) perspective(1px);
    opacity: 1;
    transition: visibility 150ms var(--ease-in-out), opacity .35s cubic-bezier(.19,1,.22,1), transform .55s cubic-bezier(.19,1,.22,1);
  }

  .ms-logo__default a {
    height: 36px !important;
  }
  .navbar-nav > .menu-item-has-children:last-child > .sub-menu {
    left: auto;
    right: 0;
    &::after {
      left: auto;
      right: 1.8rem;
    }
  }
}

}

[data-theme="dark"] {
  
  .main-header__nav .navbar-nav .sub-menu {
    background-color: var(--color-contrast-lower);
    li {
      > a {
        color: var(--color-contrast-higher) !important;

        &:hover {
          background-color: hsla(var(--color-contrast-higher-h),var(--color-contrast-higher-s),var(--color-contrast-higher-l),0.1) !important;
        }

      }
    }
    &::after {
      background-color: var(--color-contrast-lower);
    }
  }

}

.main-header__default .navbar-nav li.menu-item:last-child > a {
  padding-right: 0;
}

// Mobile Header
@media only screen and (max-width: 1023px) {

  [data-theme='dark'] {
    .main-header__nav {
      background-color: var(--color-contrast-lower);
    }
    .main-header__default .navbar-nav>.menu-item > a {
      color: var(--color-contrast-higher);
    }
    .main-header__nav--is-visible {
      &::after {
        background-color: var(--color-contrast-lower);
      }
    }
    .main-header__layout .main-header__default .navbar-nav .menu-item:hover > a {
      color: var(--color-contrast-higher);
    }
    .main-header__nav .navbar-nav .sub-menu li > a:hover {
      background-color: transparent!important;
  }
  }
  .main-header__layout .main-header__default .navbar-nav .menu-item:hover > a {
    color: var(--color-contrast-lower);
  }
  .is_mobile {
    .sub-menu .sub-menu {
      margin-left: 30px;
    }
  }
  .main-header__nav-trigger {
    padding: 0;
    border: none;
    display: flex;
    align-items: center;
    background-color: transparent;
  }
  .main-header__nav {
    margin-top: 15px;
    position: absolute;
    top: var(--main-header-height);
    left: 0;
    width: 100%;
    background-color: var(--color-contrast-higher);
    padding-top: var(--component-padding);
    padding-bottom: var(--component-padding);
    display: none;
  }
  .main-header__layout .main-header__nav .menu-item > a {
    padding: var(--space-xs) 15px;
    color: var(--color-contrast-low);
  }
  .main-header__default .menu-item-has-children > a::after {
      content: "";
      display: flex;
      justify-content: center;
      align-items: center;
      bottom: 0;
      margin-left: auto;
      width: 12px;
      height: 12px;
      background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0Ij48cGF0aCBkPSJNMTAuNDc3IDBoLTguOTc3bDEyLjAyNCAxMi0xMi4wMjQgMTJoOC45NzdsMTIuMDIzLTEyeiIvPjwvc3ZnPg==');
      background-position: center;
      background-size: cover;
      letter-spacing: 1px;
      filter: invert(100%);
      transition: transform 150ms var(--ease-in-out);
  }

  .main-header__default .menu-item-has-children > a.active::after {
    transform: rotate(90deg);
  }

  .main-header__default .navbar-nav > .menu-item > a {
    padding-top: var(--space-xs);
    padding-bottom: var(--space-xs);
    color: var(--color-contrast-lower);
    font-weight: bold;
    text-transform: uppercase;
    font-size: 18px !important;
  }

  .main-header__nav .sub-menu {
    font-size: 14px;
    font-weight: 400;
  }
  .main-header__default .navbar-nav {
    a {
      padding-top: var(--space-xs) !important;
      padding-bottom: var(--space-xs) !important;
    }
  }
  .main-header__default .navbar-nav .sub-menu {
    display:none;
  }  
}

@media only screen and (max-width: 782px) {
  .main-header {
    width: calc(100% - 1.5*var(--space-md));
    margin: 0 auto;
  }
  .main-header__logo {
    max-width: 50%;

    a {
      width: 100%;
    }
  }
  .ms-logo__default a {
    height: 26px!important;
  }
}
@media only screen and (max-width: 512px) {
  .main-header__default .navbar-nav > .menu-item.current-menu-item > a {
    color: var(--color-primary) !important;
  }

  .current-menu-item a {
    color: var(--color-primary) !important;
  }
  .main-header__admin.main-header.sticky,
  .main-header__admin.main-header.fixed {
    margin-top: 46px;
    position: absolute;
  }
}

.auto-hide-header.is-blur::after {
  backdrop-filter: blur(10px);
  backface-visibility: hidden;
  transition: background-color 250ms ease, border-color 250ms ease;
}
.auto-hide-header.is-hide {
  margin-top: calc(var(--main-header-height-md) * -1) !important;
}

// Elementor Editor
.elementor-editor-active .main-header.js-main-header.auto-hide-header {
  margin-bottom: 24px;
}