/**
 * Root Media Query Variables
 */
:root {

  /* Line Height */
  --global--line-height-body: 2.1;
  --global--line-height-heading: 1.3;
  --global--line-height-page-title: 1.1;

  /* Font Size */
  --global--font-size-base: 1.25rem;
  --global--font-size-xs: 1rem;
  --global--font-size-sm: 1.125rem;
  --global--font-size-md: 1.25rem;
  --global--font-size-lg: 1.5rem;
  --global--font-size-xl: 2.25rem;
  --global--font-size-xxl: 4rem;
  --global--font-size-xxxl: 5rem;
  --global--font-size-page-title: var(--global--font-size-xxl);
  --global--letter-spacing: normal;

  /* Headings */
  --heading--font-size-h1: var(--global--font-size-page-title);
  --heading--font-weight: normal;

  /* Widgets */
  --widget--line-height-list: 1.9;
  --widget--line-height-title: 1.4;
  --widget--font-weight-title: 700;
  --widget--spacing-menu: calc(0.66 * var(--global--spacing-unit));

  /* Spacing */
  --global--spacing-width: 810px;
  --global--spacing-unit: 20px;
  --global--spacing-measure: unset;
  --global--spacing-horizontal: 25px;
  --global--spacing-vertical: 30px;

  --responsive--spacing-horizontal: calc(2 * var(--global--spacing-horizontal) * 0.6);
  --responsive--aligndefault-width: calc(100vw - var(--responsive--spacing-horizontal));
  --responsive--alignwide-width: calc(100vw - var(--responsive--spacing-horizontal));
  --responsive--alignfull-width: 100%;
  --responsive--alignright-margin: var(--global--spacing-horizontal);
  --responsive--alignleft-margin: var(--global--spacing-horizontal);

    /* Cover block */
  --cover--height: calc(15 * var(--global--spacing-vertical));
  --cover--color-foreground: var(--global--color-white);
  --cover--color-background: var(--global--color-black);
}

@media only screen and (min-width: 482px) {

  :root {
    --responsive--aligndefault-width: min(calc(100vw - 4 * var(--global--spacing-horizontal)), var(--global--spacing-width));
    --responsive--alignwide-width: calc(100vw - 2 * var(--global--spacing-horizontal));
    --responsive--alignright-margin: calc(0.5 * (100vw - var(--responsive--aligndefault-width)));
    --responsive--alignleft-margin: calc(0.5 * (100vw - var(--responsive--aligndefault-width)));
  }

  .site-main > article > *, .site-main > .not-found > *, .entry-content > *, [class*=inner-container] > *, .wp-block-template-part > * {
    margin-top: var(--global--spacing-vertical);
    margin-bottom: var(--global--spacing-vertical);
  }

  .entry-content > .alignright {
    margin-left: var(--global--spacing-horizontal);
    margin-right: var(--responsive--alignright-margin);
  }

  .entry-content > .alignleft {
    margin-left: var(--responsive--alignleft-margin);
    margin-right: var(--global--spacing-horizontal);
  }

  .wp-block-columns .wp-block-column > * {
    margin-top: var(--global--spacing-vertical);
    margin-bottom: var(--global--spacing-vertical);
  }

}

@media only screen and (max-width: 992px) {

  :root {
    --responsive--aligndefault-width: min(calc(100vw - 2 * var(--global--spacing-horizontal)), var(--global--spacing-width));
    --responsive--alignwide-width: calc(100vw - 2 * var(--global--spacing-horizontal));
  }

}

@media only screen and (max-width: 552px) {

  :root {
    --responsive--aligndefault-width: min(calc(100vw - 1 * var(--global--spacing-horizontal)), var(--global--spacing-width));
    --responsive--alignwide-width: calc(100vw - 1 * var(--global--spacing-horizontal));
  }

}

@media only screen and (min-width: 822px) {

  :root {
    --responsive--aligndefault-width: min(calc(100vw - 8 * var(--global--spacing-horizontal)), var(--global--spacing-width));
    --responsive--alignwide-width: min(calc(100vw - 8 * var(--global--spacing-horizontal)), 1240px);
  }

}

/**
 * Extends
 */
html {
  line-height: var(--global--line-height-body);
}
body {
  overscroll-behavior: none;
}
dfn, cite, em, i {
    font-style: italic;
}

ol {
    list-style-type: decimal;
}

ul, ol {
    margin: 0;
    padding-left: calc(1 * var(--global--spacing-horizontal));
}

ul > ul {
    list-style-type: circle;
}

dd {
  margin: 0;
  padding-left: calc(2 * var(--global--spacing-horizontal));
}

header *, main *, footer * {
    max-width: var(--global--spacing-measure);
}

embed, iframe, object, video {
    max-width: 100%;
}

pre {
    background: var(--color-contrast-lower);
    font-family: "Courier 10 Pitch", Courier, monospace;
    line-height: 1.6;
    margin-bottom: 30px;
    max-width: 100%;
    overflow: auto;
    padding: 1em;
    border: solid 1px hsla(var(--color-contrast-low-h),var(--color-contrast-low-s),var(--color-contrast-low-l),.15);
    border-radius: var(--radius-md);
}

code {
    font-size: 1em;
    color: var(--color-contrast-hight);
    font-family: monospace;
}

table, .wp-block-table {
    width: 100%;
    min-width: 240px;
    border-collapse: collapse;
}

table thead,
table tfoot,
.wp-block-table thead,
.wp-block-table tfoot {
  text-align: center;
}

table td,
table th,
.wp-block-table td,
.wp-block-table th {
  padding: calc(0.5 * var(--global--spacing-unit)) calc(0.8 * var(--global--spacing-unit));
  border: 1px solid var(--color-contrast-low);
}
.gallery-caption {
  display: block;
}
.bypostauthor {
  display: block;
}

/*
 * Block & non-gutenberg content wrapper children
 * - Sets spacing-unit margins
 */
.entry-header > *,
.post-thumbnail > *,
.page-content > *,
.comment-content > *,
.widget > * {
  margin-top: var(--global--spacing-unit);
  margin-bottom: var(--global--spacing-unit);
}

.entry-header > *:first-child,
.post-thumbnail > *:first-child,
.page-content > *:first-child,
.comment-content > *:first-child,
.widget > *:first-child {
  margin-top: 0;
}

.entry-header > *:last-child,
.post-thumbnail > *:last-child,
.page-content > *:last-child,
.comment-content > *:last-child,
.widget > *:last-child {
  margin-bottom: 0;
}

.clearfix {
  margin: 0 !important;
}
/* Block Alignments */

/**
 * .alignleft
 */
.alignleft {

  /*rtl:ignore*/
  text-align: left;
  margin-top: 0;
  margin-right: var(--global--spacing-horizontal);
  margin-bottom: var(--global--spacing-vertical);
}

.entry-content > .alignleft {
  max-width: var(--responsive--aligndefault-width);
}

@media only screen and (min-width: 482px) {

  .alignleft {

    /*rtl:ignore*/
    float: left;

    /*rtl:ignore*/
    margin-right: var(--global--spacing-horizontal);
    margin-bottom: var(--global--spacing-vertical);
  }

  .entry-content > .alignleft {
    max-width: calc(50% - var(--responsive--alignleft-margin));
  }
}

/**
 * .aligncenter
 */
.aligncenter {
  clear: both;
  display: block;
  float: none;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

/**
 * .alignright
 */
.alignright {
  margin-top: 0;
  float: right;
  margin-left: var(--global--spacing-horizontal);
  margin-bottom: var(--global--spacing-vertical);
}

.entry-content > .alignright {
  max-width: var(--responsive--aligndefault-width);
}

@media only screen and (min-width: 482px) {

  .alignright {

    /*rtl:ignore*/
    float: right;

    /*rtl:ignore*/
    margin-left: var(--global--spacing-horizontal);
  }

  .entry-content > .alignright {
    max-width: calc(50% - var(--responsive--alignright-margin));
  }
}

[class*=inner-container] > .alignleft + *,
[class*=inner-container] > .alignright + * {
  margin-top: 0;
}

/**
 * .alignwide
 */
.alignwide {
  clear: both;
}

/**
 * .alignfull
 */
.alignfull {
  clear: both;
}

.has-left-content {
  justify-content: flex-start;
}

.has-right-content {
  justify-content: flex-end;
}

.has-parallax {
  background-attachment: fixed;
}

.has-drop-cap:not(:focus)::first-letter {
  font-weight: var(--heading--font-weight);
  line-height: 0.66;
  text-transform: uppercase;
  font-style: normal;
  float: left;
  margin: 0.15em 0.2em 0 0;
  font-size: calc(1.2 * var(--heading--font-size-h1));
}

.has-drop-cap:not(:focus)::after {
  content: "";
  display: table;
  clear: both;
  padding-top: 14px;
}

.desktop-only {
  display: none;
}
@media only screen and (min-width: 482px) {

  .desktop-only {
    display: block;
  }
}

.entry-content img {
    max-width: 100%;
}
.entry-content ul {
    line-height: var(--global--line-height-body);
}
.entry-content .wp-block-gallery  {
  margin-top: 0;
}

// /-----------------------------/

.post-thumbnail,
.entry-content .wp-audio-shortcode,
.entry-content > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.wp-block-separator):not(.woocommerce),
*[class*=inner-container] > *:not(.entry-content):not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.wp-block-separator):not(.woocommerce),
