  max-width: var(--responsive--aligndefault-width);
  margin-left: auto;
  margin-right: auto;
}
.clear:after, .entry-content:after, .comment-content:after, .site-header:after, .site-content:after, .site-footer:after {
    content: '';
    display: block;
    clear: both;
}
.widget-area, .pagination, .comments-pagination, .post-navigation, .site-footer, .site-header, .alignwide, .wide-max-width {
    max-width: var(--responsive--alignwide-width);
    margin-left: auto;
    margin-right: auto;
}

.entry-content {
  word-wrap: break-word;
  line-height: var(--global--line-height-body);
}
.ms-default-page.entry-content {
  padding-left: calc(var(--bs-gutter-x)/ 2)!important;
  padding-right: calc(var(--bs-gutter-x)/ 2)!important;
}

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

.entry-content {

    > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide):not(.size-full):not(.video-container):not(.gallery) {
        max-width: var(--responsive--aligndefault-width);
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
    }
    .wp-block-audio, .wp-block-video {
        margin-bottom: calc(var(--space-md) * var(--text-vspace-multiplier))
    }
    a.wp-block-file__button {
      font-size: 1em;
    }
    .wp-block-video figcaption {
      font-size: 14px;
    }
    a:not(.wp-block-button__link):not(.tag-cloud-link):not(.ms-s-i):not(.ms-sl) {
      text-decoration: underline;
    }

}

.alignwide {
    clear: both;
}

.wp-block-cover, .wp-block-cover-image {
    min-height: var(--cover--height);
    margin-top: inherit;
}

.wp-block-image figcaption {
  color: var(--color-contrast-medium);
  margin: 0;
  padding: 0.5rem;
  text-align: center;
  font-size: 14px;
}

.blocks-gallery-grid .blocks-gallery-image figcaption,
.blocks-gallery-grid .blocks-gallery-item figcaption,
.wp-block-gallery .blocks-gallery-image figcaption,
.wp-block-gallery .blocks-gallery-item figcaption {
