}
.text-component__inner > .video-container { 
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    margin-bottom: 30px;
}
.text-component__inner > .video-container iframe,
.text-component__inner > .video-container object,
.text-component__inner > .video-container embed,
.text-component__inner > .video-container video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.wp-block-spacer {
    display: block;
    margin-bottom: 0 !important;
    margin-top: 0 !important;
}

// Post Title
.ms-sp--header {
  margin-top: 8vh;
  margin-bottom: 8vh;

  h1,
  .post-meta-date.meta-date-sp {
    max-width: var(--responsive--aligndefault-width);
    margin-left: auto;
    margin-right: auto;
  }
  .post-meta-date.meta-date-sp {
    display: flex;
    justify-content: center;
    font-size: 15px;
  }
  .ms-sp--title {
    font-weight: 800;
    font-size: calc(var(--text-xxxl) + 11px);
    margin-bottom: 8pt;
    word-wrap: break-word;
    text-align: center;
    text-transform: uppercase;
  }
  
}
.ms-single-post .ms-sp--header {
  margin-bottom: 40px;

  .post-category__list {
    max-width: var(--responsive--aligndefault-width);
    margin: 0 auto;
    .post-categories {
        list-style: none;
        padding-left: 0;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        li {
            background-color: var(--color-primary);
            padding: 0 8pt 2pt;
            border-radius: 3pt;
            margin-bottom: 8pt;
            transition: background-color .6s cubic-bezier(.19,1,.22,1);

            a {
                font-size: 13px;
                font-weight: 600;
                text-transform: uppercase;
                color: var(--color-white);
            }

            &:hover {
              background-color: var(--color-primary-dark);
            }
            
        }

        li:not(:last-child) {
            margin-right: 8pt;
        }

    }
}

}

// Archive Title
.search .ms-sp--header,
.blog .ms-sp--header {
  .ms-sp--title {
    margin-left: auto;
    margin-right: auto;
    max-width: var(--responsive--alignwide-width);
    padding-right: calc(var(--bs-gutter-x)/ 2);
    padding-left: calc(var(--bs-gutter-x)/ 2);
  }
}
.archive {
  .ms-sp--header {
    margin-left: auto;
    margin-right: auto;
    max-width: var(--responsive--alignwide-width);
    padding-right: calc(var(--bs-gutter-x)/ 2);
    padding-left: calc(var(--bs-gutter-x)/ 2);

    h1 {
      max-width: 100%;
    }
  }
}

.ms-sp-list {
  .grid-item:last-child {
    margin-top: var(--space-xl);
  }
}

// Post Image
.ms-single-post--img {
  max-width: var(--responsive--alignwide-width);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  overflow: hidden;
}

/**
 * Block: Cover
 */ 
.wp-block-cover__inner-container {
  p {
    margin-top: 0;

    a {
      color: var(--color-white);
      text-decoration: underline;
      text-decoration-style: solid;

      &:hover {
        opacity: .7;
        text-decoration-style: dotted;
      }
    } 
  }    
}

.wp-block-cover:not([class*=background-color]) .wp-block-cover__inner-container, .wp-block-cover:not([class*=background-color]) .wp-block-cover-image-text, .wp-block-cover:not([class*=background-color]) .wp-block-cover-text, .wp-block-cover-image:not([class*=background-color]) .wp-block-cover__inner-container, .wp-block-cover-image:not([class*=background-color]) .wp-block-cover-image-text, .wp-block-cover-image:not([class*=background-color]) .wp-block-cover-text {
