}
.wp-block-columns .wp-block-column > *:last-child {
    margin-bottom: 0;
}

.wp-block-image img {
    display: block;
}
.wp-caption-text {
  color: var(--color-contrast-medium);
  padding: 0.5rem;
  text-align: center;
  font-size: 14px;
  line-height: 1.6;
  margin-top: calc(0.35 * var(--global--spacing-unit));
  margin-bottom: var(--global--spacing-unit);
}
.blocks-gallery-caption {
  color: var(--color-contrast-medium);
