  }
}

.wp-block-latest-posts__list {
  padding-left: 0;
}
.wp-block-latest-posts__post-date,
.wp-block-latest-comments .wp-block-latest-comments__comment-date {
    color: var(--color-contrast-medium);
    font-size: 14px;
