// Search
.wp-block-search .wp-block-search__input {
  border: 1px solid var(--color-contrast-low);
  border-radius: 0.25rem;
  background-color: transparent;
  transition: border-color 150ms var(--ease-in-out), box-shadow 150ms var(--ease-in-out);

  &:hover {
    border-color: var(--color-primary);
  }

  &:focus-visible {
    --color-shadow: hsla(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l), 0.2);
    z-index: 1;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 4px var(--color-shadow);
    outline: none;
  }

}
.wp-block-search__inside-wrapper {
  position: relative;
}
.wp-block-search button.wp-block-search__button {
  margin-left: 0;
  line-height: 1;
  transition: box-shadow 150ms var(--ease-in-out);
  position: absolute;
  width: 44px;
  height: 40px;
  left: 2px;
  top: 2px;
  overflow: hidden;
  border: none;
  padding: 15px;
  font-size: 0px;
  z-index: 1;
  background: transparent;

  &::after {
    content: '';
    position: absolute;
    width: 24px;
    height: 24px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-size: cover;
    background-image: url("data:image/svg+xml,%3Csvg%20focusable%3D%22false%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2024%2024%22%20fill%3D%22%239698a2%22%3E%0A%3Cpath%20d%3D%22M15.5%2014h-.79l-.28-.27A6.471%206.471%200%200%200%2016%209.5%206.5%206.5%200%201%200%209.5%2016c1.61%200%203.09-.59%204.23-1.57l.27.28v.79l5%204.99L20.49%2019l-4.99-5zm-6%200C7.01%2014%205%2011.99%205%209.5S7.01%205%209.5%205%2014%207.01%2014%209.5%2011.99%2014%209.5%2014z%22%3E%0A%3C%2Fpath%3E%0A%3C%2Fsvg%3E");
    background-repeat: no-repeat;
  }
  &:focus,
  &:focus-visible {
    outline: none !important;
  }

  &:hover {
    cursor: pointer;
  }
}
.wp-block-search {
    .wp-block-search__label {
        margin-bottom: calc(var(--space-xs) * var(--text-vspace-multiplier));
    }
    .wp-block-search__input {
      padding:  0.5em 0.75em 0.5em 44px;
    }
}

// RSS
.wp-block-rss {
  padding-left: 0;

    .wp-block-rss__item {
      list-style: none;
        padding-left: 0;
        &::before {
            content: none !important;
        }
    }
}


// Tag Cloud
.tagcloud,
.single-post__tags,
.wp-block-tag-cloud {
  a {
    background-color: var(--color-contrast-lower);
    color: var(--color-contrast-higher);
    float: left;
    border: solid 1px hsla(var(--color-contrast-low-h),var(--color-contrast-low-s),var(--color-contrast-low-l),.15);
    border-radius: 4px;
    font-size: 14px !important;
    padding-left: var(--space-sm);
    padding-right: var(--space-sm);
    margin-right: 8px;
    margin-bottom: 8px;
    transition: all .3s var(--ease-in-out);
    line-height: 2;

    &::before {
      content: '#';
    }

    &:hover {
      background-color: hsla(var(--color-contrast-high-h),var(--color-contrast-high-s),var(--color-contrast-high-l),0.15);
      color: var(--color-contrast-high) !important;
    }
  }

  &::after {
    content: '';
    display: block;
    clear: both;
  }
}

.single-post__tags {
  padding-bottom: var(--global--spacing-vertical);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: calc(20px + var(--global--spacing-vertical));
  max-width: var(--responsive--aligndefault-width);
  border-bottom: 1px solid var(--color-contrast-low);
}

// Post Footer Navigation
.navigation.post-navigation {
  position: relative;
}
.nav-links {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  max-width: var(--responsive--aligndefault-width);

    .nav-previous,    
    .nav-next {
      flex-basis: calc( 50% - 0.01px - var(--grid-gap, 1em));
      max-width: calc( 50% - 0.01px - var(--grid-gap, 1em));
      display: flex;

        h3 {
            transition: color 150ms var(--ease-in-out);
            display: block;
            word-break: break-word;
            font-size: 22px;
            font-weight: 700;
            text-transform: uppercase;

            &:hover {
                color: var(--color-primary);
            }
        }
    }
    .nav-previous {
      margin-left: 0;
      margin-right: auto;

      .ms-spp--i {
        position: relative;
        display: flex;
        overflow: hidden;
        margin-right: var(--space-sm);
        flex-shrink: 0;

        svg {
          fill: var(--color-primary);
          position: absolute;
          top: 0;
          left: -80px;
          width: 40px;
          height: 80px;
          padding: 5px;
          background-color: rgba(255,255,255,.4);
          backdrop-filter: saturate(180%) blur(10px);
          transform: rotate(-180deg);
          border-radius: 8px;
          transition: all 300ms ease;
        }

      } 
      &:hover svg {
        left: 0;
      }
    }
    .nav-next {
      position: relative;
      justify-content: end;

      .ms-spn--i {
        position: relative;
        display: flex;
        overflow: hidden;
        flex-shrink: 0;

        svg {
          fill: var(--color-primary);
          position: absolute;
          top: 0;
          right: -80px;
          width: 40px;
          height: 80px;
          padding: 5px;
          background-color: rgba(255,255,255,.4);
          backdrop-filter: blur(10px);
          border-radius: var(--radius-md);
          transition: all 300ms ease;
        }

      }
      &:hover svg {
        right: 0;
      }
    }
}
.nav-next .nav-label {
    text-align: right;
}
.nav-label {
  display: inline-block;
  font-size: 15px;
  margin-bottom: 6pt;
  text-decoration: none;
  color: var(--color-contrast-high);
}
.prev-post {
  display: flex;
  align-items: center;

  img {
    border-radius: var(--radius-lg);
    margin-right: var(--space-sm);
  }

}
.next-post {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;

  img {
    border-radius: var(--radius-lg);
    margin-left: var(--space-sm);
  }
}

.edit-link {
  max-width: 1240px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-bottom: var(--global--spacing-vertical);
  display: inline-block;
}

@media only screen and (max-width: 512px) {
    .nav-links .nav-next,
    .nav-links .prev-post {

        h3 {
            font-size: 18px;
        }
    }

    .text-component .wp-block-image figcaption {
        margin-top: 1rem;
    }

    .text-component ol li {
        padding-bottom: 0;
        margin-bottom: 0;
    }
}

.no-thumb {
    padding: 0 15px 30px;
    .card__content--text {
        background-color: var(--color-bg);
        border-radius: var(--radius-md);
        display: block;
        padding: 10px 0;
        box-shadow: 0 1px 1px rgba(0,0,0,.15), 0 2px 2px rgba(0,0,0,.1), 0 4px 4px rgba(0,0,0,.05), 0 8px 8px rgba(0,0,0,.08), 0 16px 16px rgba(0,0,0,.02);
    }
    .card__date {
        font-size: 14px;
        opacity: .85;
        padding: 0 15px;
    }
    .ms-text-bg {
        display: flex;
        padding: 0 15px;
        color: var(--color-contrast-high) !important;
    }
    
}

.gallery.gallery-size-thumbnail {
    margin-left: -15px;
    margin-bottom: 15px;
}

/*
 * .entry-content children specific controls
 * - Adds special margin overrides for alignment utility classes
 */

.entry-content > *.alignleft,
.entry-content > *.alignright,
.entry-content > *.alignleft:first-child + *,
.entry-content > *.alignright:first-child + *,
.entry-content > *.alignfull.has-background {
    margin-top: 0;
}

.entry-content > *:last-child:not(.wp-block-media-text),
.entry-content > *.alignfull.has-background {
    margin-bottom: 0;
}

.entry-content > *.alignfull + .alignleft,
.entry-content > *.alignfull + .alignright {
    margin-top: var(--global--spacing-vertical);
}

// Post Edit Link
.post-edit-link {
  .meta-icon {
    display: flex;

    svg {
      margin-right: 4pt;
      fill: var(--color-primary);
    }
  }
}

// Blockquote
.ms-sp--article,
.ms-default-page {

    blockquote {
        padding: 1em;
    }
    blockquote.is-large,
    blockquote.is-style-large {
        padding: 1.5rem !important;
        border: solid 2pt;
        border-color:  var(--color-primary);
    }

    .wp-block-pullquote.has-background blockquote,
    .wp-block-pullquote:not(.is-style-solid-color) blockquote {
        border: none;
        padding-top: 0;
        padding-bottom: 0;

        &::after {
            content: none;
        }
    }

}

// Page BLock Unitest
.wp-block-separator.is-style-dots:before {
  padding-left: 1em;
}

.wp-block-categories li {
  text-align: left;
}

.wp-block-table tr:nth-child(odd) {
    background-color: #f2f2f2;
}

// Related Posts Section

.ms-related-posts {
  background-color: var(--color-contrast-lower);
  padding: calc(var(--space-lg) + 10px) 0;
  margin-top: calc(var(--space-lg) + 10px);

  .ms-rp--block {
    width: 100%;
    overflow: hidden;
    position: relative;
    border-radius: 8px;
    border: solid 1px hsla(var(--color-contrast-low-h),var(--color-contrast-low-s),var(--color-contrast-low-l),0.15);
    background-color: var(--color-bg);

    a {
      width: 100%;
      display: flex;
      padding: 15px;
      h3 {
        transition: color 150ms var(--ease-in-out);
      }
      &:hover {
        h3 {
          color: var(--color-primary);
        }
      }

    }
    .ms-rp--icon {
      position: absolute;
      width: 30px;
      height: 30px;
      right: 10px;
      top: 50%;
      opacity: 0;
      fill: var(--color-contrast-high);
      -webkit-transform: translateY(-50%) translateX(-50%);
      -ms-transform: translateY(-50%) translateX(-50%);
      transform: translateY(-50%) translateX(-50%);
      will-change: transform;
      transition: transform 300ms var(--ease-in-out), opacity 300ms var(--ease-in-out), fill 500ms var(--ease-in-out);
    }
    .ms-rp--icon-2 {
      position: absolute;
      width: 30px;
      height: 30px;
      right: 10px;
      top: 50%;
      fill: var(--color-contrast-high);
      transform: translateY(-50%);
      will-change: transform;
      transition: transform 300ms var(--ease-in-out), opacity 300ms var(--ease-in-out);
    }
    &:hover .ms-rp--icon {
      opacity: 1;
      fill: var(--color-primary);
      transform: translateY(-50%) translateX(0%);
    }
    &:hover .ms-rp--icon-2 {
        opacity: 0;
        transform: translateY(-50%) translateX(50%);
    }
  }

  .ms-rp--block + .ms-rp--block {
    margin-top: 16pt;
  }

  .ms-rp--thumb {
    display: block;
    overflow: hidden;
    border-radius: 4px;
    max-width: 164px;
    height: 92px;
    width: 100%;
    margin-right: 8pt;

  }

    .ms-rp--cont {
      display: flex;
      align-items: center;
      overflow: hidden;
      position: relative;
      transition: border-color 250ms var(--ease-in-out);

      .ms-rp--text {
        line-height: 1;
        position: relative;
        overflow: hidden;
        border-radius: var(--radius-md);
        z-index: 2;
        color: var(--color-contrast-high);
        transition: color 500ms var(--ease-in-out);
        padding: 1em;
      }

      .ms-rp__date {
        font-size: 15px;
        color: var(--color-contrast-medium);
      }

      .ms-rp__title {
        font-size: 22px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 4pt;
        transition: color 500ms var(--ease-in-out);
        padding-right: 35px;
      }

    }

  .ms-rp--title {
    font-weight: bold;
    max-width: var(--responsive--aligndefault-width);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    line-height: 1;
    text-transform: uppercase;
  }
  .alignwide {
    max-width: var(--responsive--aligndefault-width);
  }
}

.ms-sl {
  transition: color 150ms var(--ease-in-out);
}

// Mobile
@media (max-width: 975px) {
  .blog .ms-sp--header .ms-sp--title {
    padding: 0;
    max-width: 100%;
  }
  .archive .ms-sp--header {
    max-width: 100%;
  }
}
@media (max-width: 552px) {
  .ms-single-post--img,
  .ms-single-post .ms-sp--header {
    margin-bottom: 30px;
  }
  .post-meta-date.meta-date-sp {
    font-size: 14px !important;
  }
  .ms-sp--header .ms-sp--title {
    font-size: var(--text-xxl,2.074em);
  }
  .ms-sp--header {
    margin-bottom: 30px;
  }
  .ms-default-page.entry-content + .entry-footer {
    padding-left: calc(var(--bs-gutter-x)/ 2)!important;
    padding-right: calc(var(--bs-gutter-x)/ 2)!important;
  }
}

// Additional classes
.has-sticky-column {

  .elementor-widget-wrap {
    position: -webkit-sticky;
    position: sticky;
    height: max-content;
    top: var(--main-header-height-md);
  }

}

body[data-theme='dark'] .menu-item-has-children>a::after{
  filter: invert(100%);
}

iframe {
  transition: opacity 500ms ease-in-out;
  transition-delay: 250ms;
}