/* -------------------------------- 

Title: Comments Style
Descr: Design Comments

-------------------------------- */
.ms-section__comments {
  width: 100%;
  max-width: var(--responsive--aligndefault-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding: calc(var(--space-lg) + 10px) 0;
  
  .ms-comments-title {
    font-weight: 700;
    font-size: calc(var(--text-xl,1.728em) + 1px);
    text-transform: uppercase;
    margin-bottom: var(--space-sm);
    font-family: var(--font-heading);
  }
  .required-field-message {
    margin-left: 4pt;
  }
}
.form-submit .btn {
  border-radius: 4px;
}
.ms-comment-list {
  padding-left: 0;
  width: 100%;
  position: relative;
  margin-bottom: var(--space-lg);
  list-style: none;
}
.ms-author-vcard--info {
  display: flex;
  align-items: center;

  .ms-author-name {
    font-size: 18px;
    font-weight: 700;
  }
  .ms-comment-time {
    margin-left: 12pt;
    font-size: 15px;
    margin-top: 1pt;
    color: var(--color-contrast-medium);
  }
}

.ms-comment-body {
  font-size: var(--text-sm);
  position: relative;
  margin: 30px 0;
  display: flex;
}
.ms-comment-body:last-child {
  margin-bottom: 0;
}
.ms-author-vcard {
  margin-right: 20px;
}
.ms-author-vcard-content {
  width: 100%;
}
.ms-commentcontent {
  font-size: 16px;
  margin-top: 12pt;
  padding: 15px 20px;
  line-height: 1.8;
  background-color: var(--color-contrast-lower);
  border-radius: 8px;
  > *:not(blockquote p) {
    margin-bottom: calc(var(--global--spacing-vertical) - 10px);
  }
  img {
    padding: 6pt 0;
  }
}
.ms-comment-footer {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.comment-edit-link,
.ms-comment-footer a {
  margin-right: var(--space-xxxs);
  font-size: 14px;
  text-decoration: none;
  display: flex;
  color: var(--color-primary);
  transition: opacity 250ms ease;
  svg {
    fill: var(--color-primary);
    margin-right: var(--space-xxxs);
  }
  &:hover {
    opacity: .75;
  }
}
.ms-comment-edit a {
  margin-right: 12pt;
}
.comment-reply-link {
  .group {
    fill: var(--color-primary);
    margin-right: var(--space-xxxs);
  }
}
.ms-comment-footer div:last-child a {
  margin-right: 0;
}
.ms-comment-list .children {
  margin-left: var(--space-lg);
  list-style: none;
}
.comment-notes {
  font-size: 14px;
  color: var(--color-contrast-medium);
}
.comment-notes,
.logged-in-as {
  margin-top: 15px;
  margin-bottom: 30px;
}
.comment-form-cookies-consent {
  font-size: 14px;
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 30px;

    #wp-comment-cookies-consent {
      margin-right: 6pt;
      margin-top: 2pt;
    }
}
.post.pingback {
  margin: var(--space-md) 0;
  .post-date {
    font-size: 14px;
    color: var(--color-contrast-medium);
    margin-bottom: var(--space-sm);
  }
}
#reply-title {
  font-weight: bold;
  text-transform: uppercase;
}
li + .comment-respond {
  margin-top: 30px;
}

#cancel-comment-reply-link {
  letter-spacing: 0;
  padding-left: var(--space-xxs);
  font-size: 16px;
  font-weight: 400;
  color: var(--color-error);
  &:hover {
    opacity: .75;
  }
}

@media only screen and (max-width: 552px) {
  .ms-section__comments {
    max-width: inherit;
    padding-right: calc(var(--bs-gutter-x)/ 2)!important;
    padding-left: calc(var(--bs-gutter-x)/ 2)!important;   
  }
  .comment-notes {
    padding-left: 2pt;
    margin-top: 4pt;
  }
  .comment-form-cookies-consent {
    margin-left: 1.2rem;
    margin-bottom: 20px;
  }
  .avatar.avatar--lg {
    display: none;
  }
  .ms-author-vcard {
    margin-left: 0;
  }
  .ms-comment-list .children {
    margin-left: 0;
  }
}