/* -------------------------------- 
Post Feed Card Style
-------------------------------- */
.ms-posts--card {
    --bs-gutter-x: 3rem;
    --bs-gutter-y: 3rem;

    .grid-item {

        figure {
            position: relative;
            border-radius: var(--radius-lg);
            overflow: hidden;

            svg {
                position: absolute;
                z-index: 3;
                height: 24px;
                top: 0;
                right: 0;
                width: auto;
                transform: rotate(-45deg);
                margin-left: 1.6rem;
                margin-left: 24px;
            }

        }
        .post-excerpt {
            border-bottom: solid 1px hsla(var(--color-contrast-higher-h),var(--color-contrast-higher-s),var(--color-contrast-higher-l),var(--color-o,.06));
            padding-bottom: calc(.8 * var(--space-md));
        }
        a {
            line-height: 2;
        }
        .ms-p--ttr {
            font-size: 13px;
            font-weight: 600;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 2.1pt var(--space-xs);
            position: absolute;
            right: var(--space-sm);
            top: var(--space-sm);
            letter-spacing: 1px;
            border-radius: 3pt;
            background-color: rgba(255,255,255,.5);
            backdrop-filter: saturate(180%) blur(1rem);
            color: #000;
        }
        a + .post-meta-cont {
            margin-top: calc(0.75 * var(--space-md));
        }
        .post-meta-cont {
            transition: border-color 250ms var(--ease-in-out);

            h3 {
                font-size: 24px;
                font-weight: 800;
                margin-top: 4pt;
                text-transform: uppercase;
                transition: color 150ms var(--ease-in-out);
            }
            p {
                font-size: 16px;
                margin-top: calc(0.8 * var(--space-md));
            }
            .post-meta-footer {
                display: flex;
                font-size: 15px;
                margin-top: calc(0.8 * var(--space-md));
                color: var(--color-contrast-medium);

                .post-meta__link {
                    margin-left: auto;
                    margin-right: 0;
                }
            }
            .post-category {
                text-transform: capitalize;
                color: var(--color-contrast-higher);
                margin-bottom: var(--space-sm);

                a {
                    font-size: 15px;
                    color: var(--color-contrast-medium);
                    &:hover {
                        color: var(--color-primary);
                    }
                }
            }

        }

        .post-meta-date {
            margin-top: var(--space-md);
        }

        h3:hover {
            color: var(--color-primary);
        }

    }

    .ms-sticky {
        left: var(--space-sm) !important;
        top: var(--space-sm) !important;
        right: auto !important;
    }

    .ms-sticky.no-thumbnail {
        left: 0 !important;
        padding: 4pt 8pt;
    }
    .no-thumbnail + .post-meta-cont {
        margin-top: var(--space-md);
    }

    .pagination {
        margin-top: var(--space-xxl);
    }
}