.ms-posts--list,
.ms-posts--default {
    padding: 0 15px;
    a + .post-content,
    .ms-post-media__link + .post-content,
    .ms-post-media__audio + .post-content,
    .ms-post-media__quote + .post-content,
    .ms-post-media__video + .post-content,
    .ms-post-media__gallery + .post-content {
        padding: var(--space-md) 0 0;
    }
    .grid-item {
        width: 100%;
        position: relative;
        margin-bottom: 4vh;
        padding-bottom: 4vh;
        border-bottom: 1px solid hsla(var(--color-contrast-higher-h), var(--color-contrast-higher-s), var(--color-contrast-higher-l),var(--color-o,.15));

        .post-category__list {

            .post-categories {
                list-style: none;
                padding-left: 0;
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 8pt;

                li {
                    background-color: var(--color-primary);
                    padding: 0 6pt 2pt;
                    border-radius: 4pt;
                    margin-bottom: 8pt;
                    transition: background-color .6s cubic-bezier(.19,1,.22,1);

                    a {
                        font-size: 13px;
                        font-weight: 600;
                        text-transform: uppercase;
                        color: var(--color-white);
                    }
                    
                    &:hover {
                        background-color: var(--color-primary-dark);
                    }

                }

                li:not(:last-child) {
                    margin-right: 8pt;
                }

            }
        }

        .post-category__list.top {
            position: absolute;
            top: 12pt;
            left: 14pt;
            z-index: 2;
            margin-bottom: 0;
        }

        .post-meta {
            display: flex;
            margin-bottom: 8pt;
            font-size: 15px;
            color: var(--color-contrast-high);

            .post-meta__author {
                display: inline-flex;
                align-items: center;
    
                &::after {
                    content: "";
                    height: 5px;
                    width: 5px;
                    display: block;
                    margin-top: 2pt;
                    margin-left: 8pt;
                    margin-right: 8pt;
                    background-color: var(--color-contrast-high);
                    border-radius: 50%;
                    opacity: 0.5;
                }
    
            }

            .ms-p--ttr  {
                display: flex;
                align-items: center;
                &::before {
                    content: "";
                    height: 5px;
                    width: 5px;
                    display: block;
                    margin-top: 2pt;
                    margin-left: 8pt;
                    margin-right: 8pt;
                    background-color: var(--color-contrast-high);
                    border-radius: 50%;
                    opacity: 0.5;
                }
            }
        }

        h2 {
            font-weight: 800;
            text-transform: uppercase;
            transition: color 150ms var(--ease-in-out);
            word-wrap: break-word;
            max-width: 100%;
            word-break: break-all;
            font-size: 32px;

            &:hover {
                color: var(--color-primary);
            }

        }
        
        p {
            margin-top: 16pt;
            font-size: 1rem;
        }

    }

    .grid-item:last-child {
        border-bottom: none;
    }

    .post-footer {
        margin-top: 16pt;
        align-items: center;
        display: flex;
        color: var(--color-contrast-medium);
    }

    a + .post-footer {
        margin-top: 8pt;
    }

    img {
        border-radius: 8pt !important;
    }
}

// Elementor
.ms-posts--wrap {
    .ms-posts--list,
    .ms-posts--default {
        padding: 0;
    }
    .ms-posts--list {
        .grid-item h2 {
            word-break: normal;
        }
    }
}
.ms-posts--default {
    .post-meta {
        margin-top: 20pt;
    }

    .post-top + .post-meta {
        margin-top: 0;
    }
}

.ms-single-post,
.ms-posts--card,
.ms-posts--card_2,
.ms-posts--default,
.ms-posts--list {

    .ms-sticky.no-thumbnail {
        position: relative;
        padding: 0;
        top: 0;
        left: 0;
        margin-bottom: 8pt;
    }
    .ms-sticky {
        font-size: 14px;
        text-transform: uppercase;
        font-weight: 700;
        display: flex;
        color: #3a3d4e;
        padding: 0 var(--space-xs);
        background-color: var(--color-warning);
        border-radius: 3pt;
        padding: 2pt;

        .ms-sticky--icon {
            display: flex;
            svg {
                width: 20px;
                fill: var(--color-primary);
                flex-shrink: 0;
                height: 16px;
                margin: 1.2pt 0;
            }
        }
    }

    .post-top {
        display: flex;
        align-items:flex-start;
        align-content:flex-start;

        .ms-sticky.no-thumbnail {
            padding: 3pt;
            margin-right: 8pt;
        }

    }

    span[role=separator] {
        width: 5px;
        height: 28px;
        display: flex;
        align-items: center;

        &::after {
            content: '';
            height: 5px;
            width: 5px;
            display: block;
            background-color: var(--color-contrast-low);
            border-radius: 50%;
        }
    }

    .post-meta-date {
        display: flex;
        align-items:flex-start;
        align-content:flex-start;
        color: var(--color-contrast-high);
        span:not(:last-child) {
            flex-shrink: 0;
        }

    }
    .post-meta-date.meta-date-sp {

        .post-author__name {
            position: relative;
            display: flex;
            align-items: center;
        }
        .post-author__name::after {
            content: "";
            height: 5px;
            width: 5px;
            display: block;
            margin-top: 1pt;
            margin: 2pt 8px 0;
            background-color: var(--color-contrast-high);
            border-radius: 50%;
            opacity: 0.5;
        }


    }
    h3 {
        word-wrap: break-word;
    }

    p {
        line-height: var(--body-line-height);
    }

    .post-meta-date {

        .post-category a:first-child::before {
            content: '';
            position: absolute;
            top: calc(50% - 1pt);
            left: -.85rem;
            height: 5px;
            width: 5px;
            display: block;
            background-color: var(--color-contrast-low);
            border-radius: 50%;
        }
    }

}

.ms-pc2--thumb {
    .ms-sticky {
        padding: 0;
    }
}

.ms-pc2--thumb,
.ms-posts--card,
.ms-posts--default {
    .ms-sticky:not(.no-thumbnail) {
        position: absolute;
        z-index: 1;
        top: 12pt;
        right: 14pt;
    }
}

