
:root {
  --icon-btn-fill: var(--color-contrast-medium);
}
body[data-theme="dark"] {
  --icon-btn-fill: hsl(210 15% 90%);
}

.back-to-top {
  position: fixed;
  width: 50px;
  height: 50px;
  right: 30px;
  bottom: 30px;
  z-index: 2;

  .ms-btt--inner {
    display: flex;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    background-color: var(--color-bg-darker);
    cursor: pointer;
  }

  .ms-btt-i {
    z-index: 1;
    width: 24px;
    left: 0;
    right: 0;
    margin: auto;
    top: 0;
    bottom: 0;
    stroke: transparent;
    fill: var(--icon-btn-fill);
    transform: translateZ(0);
  }
}

:root, [data-theme="dark"] {
  .back-to-top {
    mix-blend-mode: normal;
  }
}

.back-to-top {
  .ms_tm--tooltip {
    top: 8pt;
  }
}

@media (max-width: 992px) {
  .back-to-top {
    right: calc(1.5rem/ 2);
    bottom: calc(1.5rem/ 2);   
  }
}

@media only screen and (max-width: 512px) {
  .back-to-top {
    display: none;
  }
}