      justify-content: center;
      .grid-item {
        padding-left: 0;
        padding-right: 0;
    }
    }

    .mfp-img {

      .media-wrapper img {
          height: 100%;
      }
    }
}
.mfp-img.default {
  pointer-events: none;
}
.ms-masonry-gallery {
  position: relative;
  height: auto;

  &:after {
    content: '';
    display: block;
    clear: both;
  }
  .grid-sizer {
    display: none;
  }
  .grid-sizer,
  .grid-item {
    display: flex;
    padding: 0;

    .mfp-img {
      display: flex;
      width: 100%;
    }
  }
  .grid-item {
    float: left;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.mfp-img.jg-entry.jg-entry-visible img {
  width: 100% !important;
}
// Hover effect
.ms-content--portfolio .blockgallery:not(.justified-gallery),
.ms-masonry-gallery.blockgallery {
  .mfp-img {
    width: 100%;
    height: 100%;
    display: flex;
    position:relative;
  }
  .mfp-img figure {
    width: 100%;
  }
  .mfp-img img {
    z-index: 1;
    transition: all .3s !important;
    transform: translateZ(0) scale(1, 1);
    backface-visibility: hidden;
  }
}

// Style 1
.blockgallery.h_s1 {
  .mfp-img img {
    backface-visibility: hidden;
  }
  .mfp-img:hover img {
    opacity: .8;
  }
}
// Style 2
.blockgallery.h_s2 {
  .mfp-img:hover img {
    opacity: 1 !important;
  }
}
// Style 3
.blockgallery.h_s3 {

  .mfp-none::after,
  .mfp-img::after,
  .jg-entry::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    box-shadow: 0px 0px 0px 2pt inset;
    transition: opacity .3s;
  }
  .mfp-none:hover::after,
  .mfp-img:hover::after,
  .jg-entry:hover::after {
    opacity: 1;
  }
}