.ms_fuw--wrapper {
    position: fixed;
    display: flex;
    height: 100%;
    top: 0;
    left: 0;
    padding: 30px;
    z-index: 1;
    mix-blend-mode: difference;
    
    .ms_fuw {
        writing-mode: vertical-rl;
        transform: rotate(180deg);
        display: flex;

        .ms_fuw--list {
            display: flex;
            .socials {
                display: flex;
                padding-left: 0;
            }
            
            h5 {
                margin-bottom: 0;
                display: flex;
                align-items: center;
                font-size: 18px;
                font-weight: 400;
                letter-spacing: 1pt;
                color: var(--color-contrast-medium);

                &::after {
                    content: '';
                    width: 1px;
                    height: 40px;
                    display: block;
                    margin-bottom: 1rem;
                    margin-top: 2rem;
                    background-color: var(--color-contrast-low);
                }
            }
            li {
                list-style: none;
            }
            a {
                margin-top: 1rem;
                color: var(--color-contrast-medium);
                transition: color 250ms var(--ease-in-out);

                &:hover {
                    color: var(--color-primary);
                }
            }
            a::after {
                content: none;
            }
            // Icons
            .ms-socicon {
                transform: rotate(180deg);
            }
            [class^="socicon-"], [class*=" socicon-"] {
                font-size: 22px;
                margin-bottom: 1rem;
            }
        }
    }
}

.ms-footer {
    a {
        color: inherit;
    }
}

@media only screen and (max-width: 1392px) {
    .ms_fuw--wrapper {
        display: none;
    }
}