.portfolio_wrap {
    .subnav {
        margin-bottom: var(--space-xl);
    }

    .subnav {
        z-index: 4;
        background-color: var(--color-contrast-lower);
        display: inline-flex;
        padding: 6pt;
        border-radius: 8px;
    }
    .portfolio-feed {
        overflow: hidden;
        position: relative;
        .media-wrapper--auto {
            padding-bottom: 100%;
        }
        .load_filter {
            background: var(--color-contrast-lower);
            border: solid 1px var(--color-contrast-lower);
            position: absolute;
            top: 0;
            z-index: 1;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            left: 50%;
            opacity: 0;
            transform: translateX(-50%) translateY(0%);
            display: flex;
            visibility: hidden;
            justify-content: center;
            transition: opacity .3s cubic-bezier(.575,.015,0,.995), transform .3s cubic-bezier(.575,.015,0,.995), visibility .3s cubic-bezier(.575,.015,0,.995);
            .load-filter-icon {
                position: absolute;
            }
            .load-filter-icon:last-child {
                stroke: var(--color-primary);
            }
        }

        .load_filter.show {
            opacity: 1;
            visibility: visible;
            transform: translateX(-50%) translateY(150%);
            transition: opacity .3s cubic-bezier(.575,.015,0,.995), transform .3s cubic-bezier(.575,.015,0,.995), visibility .3s cubic-bezier(.575,.015,0,.995);
        }

        .item--inner {
            position: relative;
            img {
                width: 100%;
                display: flex;
                object-fit: cover;
            }
            .ms-p-content {
                display: block;
                flex-direction: column;
                backface-visibility: hidden;
                will-change: transform;
                h3 {
                    font-size: 20px;
                    text-transform: uppercase;
                    padding-top: var(--space-xs);
                    font-size: 24px;
                    backface-visibility: hidden;
                    will-change: font-variation-settings;
                    font-weight: 800;
                    font-variation-settings: "wght" 400;
                    transition: font-variation-settings 520ms;
                }
                .ms-p-cat {
                    color: var(--color-contrast-medium);
                    font-size: 16px;
                    display: block;
                    margin-top: 4pt;
                    text-transform: uppercase;
                }
    
            }

            &:hover .ms-p-content {
                opacity: 1;
                transition: opacity .5s .1s;
                h3 {
                    letter-spacing: 0;
                    font-variation-settings: "wght" 900;
                    transition: font-variation-settings 520ms;
                }
            }

        }

        .fadein .ms-p-content,
        .overlay .ms-p-content {
            position: absolute;
            top: 0;
            left: 0;
            padding: var(--space-md);
            width: 100%;
            z-index: 1;

                h3 {
                    padding-top: 0;
                    color: var(--color-white);
                }
                .ms-p-cat {
                    color: var(--color-white);
                    font-size: 16px;
                    font-weight: 400;
                    transform: translateX(0) translateY(0%);
                    text-transform: uppercase;
                }
        }
        .fadein .ms-p-content {
            h3::after {
                content: none;
            }
        }
        // FadeIn
        .fadein {

            .ms-p-content {
                opacity: 0;
                backface-visibility: hidden;
                will-change: transform;
                transition: opacity 1s;
                h3 {
                    font-size: 24px;
                    backface-visibility: hidden;
                    will-change: font-variation-settings;
                    font-variation-settings: "wght" 100;
                    transition: font-variation-settings 520ms;
                }
            }

            &:hover .ms-p-content {
                opacity: 1;
                transition: opacity .5s .1s;
                h3 {
                    letter-spacing: 0;
                    font-variation-settings: "wght" 900;
                    transition: font-variation-settings 520ms .1s;
                }
            }

        }
        .fadein, .overlay {
            .item--inner a {
                display: block;
            }
           .item--inner figure::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
        }

        .boxed .ms-p-content {
            margin-top: -.1pt;
            padding: var(--space-sm);
            background-color: var(--color-contrast-lower);

                h3 {
                    padding-top: 0;
                }

        }

        .fadein:not(.center) {

           .item--inner figure::after {
                content: '';
                opacity: 0;
                transition: opacity .8s;
            }

            &:hover .item--inner figure::after {
                opacity: 1;
            }

        }

        // FadeIn Center
        .fadein.center {

           .item--inner figure::after {
                content: '';
                opacity: 0;
                transition: opacity .8s;
            }

            &:hover .item--inner figure::after {
                opacity: 1;
            }
            
        }

        // FadeIn Bottom
        .h-align-middle .ms-p-content {
            margin: auto auto;
            top: 0;
            bottom: 0;
            display: flex;
            justify-content: center;
        }
        .h-align-bottom .ms-p-content {
            top: auto;
            bottom: 0;
        }  
        // Hover Effect
        .grid-item-p figure {
            overflow: hidden;
            position: relative;
        }
    }
    .ms-p--m figure {
        display: flex;

        img {
            float: left;
            object-fit: cover;
            object-position: 50% 50%;
            transition: object-position .3s ease;
        }
    }

    .ajax-area {
        margin-top: var(--space-xl);
        margin-bottom: 4px;
    }
}


// List Load More Btn

.ajax-area--list {
    margin-top: calc(var(--space-lg) + 15px);
    margin-bottom: 4px;
}
.btn-load-more {
    border-radius: 6px;
}

// Text Above
.above .item--inner {
    a {
        display: flex;
        flex-direction: column-reverse;
    }
    .ms-p-content {
        display: flex !important;
        flex-direction: column-reverse !important;

        h3 {
            padding-top: 4pt !important;
            padding-bottom: 18px;
        }
    }
    
}
// Text Below
.overlay .item--inner,
.below .item--inner {
    transition: transform  250ms ease;
    transform: translateY(0);

    .ms-p-img {
        position: relative;

        img {
            transition: transform .8s cubic-bezier(.22,1,.36,1)
        }
    }
}

.overlay .item--inner:hover,
.below .item--inner:hover {

    .ms-p-img::after {
        opacity: 1;
        top: 0rem;
    }
}

// Default
.grid-item-p:not(.fadein) .ms-p-content,
.ms-p--m .ms-p-content {
    display: flex;
    flex-direction: column;
    h3 {
    position: relative;
    display: inline-block;
    }
}
.ms-p--g2 .grid-item-p,
.ms-p--d .grid-item-p,
.ms-p--m .grid-item-p {
    &:hover h3::after {
            -webkit-transform-origin: left center;
            -ms-transform-origin: left center;
            transform-origin: left center;
            -webkit-transform: scale(1,1);
            -ms-transform: scale(1,1);
            transform: scale(1,1);
        }
}
// Zoom Effect
.portfolio_wrap .portfolio-feed .zoom .item--inner{
    .ms-p-img img{
        will-change: transform;
        transform: translateZ(0);
        transition: transform 1s;
    }
    .ms-p-img {
        overflow: hidden;
        will-change: auto;
        transition: all 1s;
    }
    &:hover .ms-p-img img {
        transform: scale(1.08);
    }
}

.portfolio_wrap .portfolio-feed .zoomout .item--inner{
    .ms-p-img img{
        will-change: transform;
        transform: translateZ(0) scale(1.08);
        transition: transform 1s;
    }
    .ms-p-img {
        overflow: hidden;
        will-change: auto;
        transition: all 1s;
    }
    &:hover .ms-p-img img {
        transform: scale(1);
    }
}

// Single Portfolio Page

// Navigation
.ms-spn--wrap {
    // position: relative;

    .ms-spn--text {
        text-align: center;
        text-transform: uppercase;
        font-weight: bold;
        h3 {
            font-weight: bold;
        }
    }
    .ms-spn--content {
        width: 100%;
        min-height: 40vh;
        height: 100%;
        display: flex;
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: center; 

        .ms-spn--link {
            position: relative;
            z-index: 2;
            font-size: 1.2vw;
            text-transform: none;
            text-align: center;
            max-width: 60vw;
            mix-blend-mode: overlay;

            h1 {
                text-transform: uppercase;
                color: var(--color-contrast-higher);
                font-weight: bold;
                transition: all 1s cubic-bezier(.575,.015,0,.995);
            }

        }

        .ms-spn--thumb {
            position: absolute;
            width: 30vw;
            height: 100%;
            max-height: 18vw;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            border-radius: 20px;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            transform-origin: bottom left;
            will-change: transform;
            transition: all 1s cubic-bezier(.575,.015,0,.995);
            clip-path: polygon(0 100%,100% 100%,100% 100%,0 100%);

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                transform: translateY(-50px) scale(1.3);
                will-change: transform;
                transition: margin-top 1s cubic-bezier(.575,.015,0,.995), transform 1s cubic-bezier(.575,.015,0,.995);
            }

            &::after {
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0,.4);
                background-color: hsla(var(--color-bg-h),var(--color-bg-s),var(--color-bg-l),.4);
                z-index: 1;
            }
        }

        .ms-spn--link:hover ~ .ms-spn--thumb {
            height: 100%;
            clip-path: polygon(0 0,100% 0,100% 100%,0 100%);

            img {
                transform: translateY(0px) scale(1);
            }
        }
    }

}
@media (max-width: 1023px) {
    .ms-spn--wrap .ms-spn--content {
        min-height: 25vh;
    }
    .ms-spn--wrap .ms-spn--content .ms-spn--thumb {
        display: none;
    }
    .ms-spn--link h1 {
        font-size: 2.4rem;
    }
}
