.ms-posts--list {

    .ms-sticky {
        padding: 2.4pt;
        margin-right: 8pt;
        position: absolute;
        z-index: 1;
        top: 12pt;
        right: 14pt;
    }

    .grid-item__thumb {
        margin-right: 25px;
        position: relative;

        .post-category__list {
            position: absolute;
            top: 12pt;
            left: 22pt !important;
            z-index: 1;
        }

        .post-thumbnail + .post-top .ms-sticky {
            position: absolute;
            z-index: 1;
            top: 12pt;
            right: 16pt;
        }
    }

    .ms-post-media__audio {
        margin-bottom: 0;
    }

    .grid-item__content {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

}
.grid-item.post_format-post-format-quote .col-lg-6 {
    width: 100%;
}
.ms-posts--list[data-order='order_2'] .grid-item {
    flex-direction: row-reverse!important;
}

.ms-posts--list[data-order='order_3'] .grid-item:nth-child(2n) {
    flex-direction: row-reverse!important;
}