.ms_theme_mode {
  position: fixed;
  bottom: 90px;
  right: 30px;
  display: flex;
  justify-content: center;
  align-items: center;

  .ms_tm--inner {
    display: flex;
    align-items: center;
    height: 50px;
    width: 50px;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--color-bg-darker);
  }
  
  .theme-toggle {
    --size: 28px;
    --icon-fill:var(--color-contrast-medium);
    --icon-fill-hover: red;
    display: flex;
    background: none;
    border: none;
    padding: 0;
    inline-size: var(--size);
    block-size: var(--size);
    aspect-ratio: 1;
    border-radius: 50%;
  
    cursor: pointer;
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
  
    outline-offset: 5px;
  
    & > svg {
      inline-size: 100%;
      block-size: 100%;
      stroke-linecap: round;
      max-width: 40px;
      max-height: 40px;
      margin: auto;
    }
  
    body[data-theme="dark"] & {
      --icon-fill: hsl(210 15% 90%);
    }
  
    @media (hover: none) {
      --size: 48px;
    }
  }

  .sun-and-moon {
    transition: transform .5s var(--ease-elastic-4);
    & > :is(.moon, .sun, .sun-beams) {
      transform-origin: center center;
    }
  
    & > :is(.moon, .sun) {
      fill: var(--icon-fill);
    }
  
    & > .sun-beams {
      stroke: var(--icon-fill);
      stroke-width: 2px;
  
    }
  
    body[data-theme="dark"] & {
      & > .sun {
        transform: scale(1.75);
      }
  
      & > .sun-beams {
        opacity: 0;
      }
  
      & > .moon > circle {
        transform: translateX(-7px);

      }
    }
  
    @media (prefers-reduced-motion: no-preference) {
      & > .sun {
        transition: transform .5s var(--ease-elastic-3);
      }
  
      & > .sun-beams {
        transition: 
          transform .5s var(--ease-elastic-4),
          opacity .5s var(--ease-3)
        ;
      }
  
      & .moon > circle {
        transition: transform .25s var(--ease-out-5);
      }
  
      body[data-theme="dark"] & {
        & > .sun {
          transform: scale(1.75);
          transition-timing-function: var(--ease-3);
          transition-duration: .25s;
        }
  
        & > .sun-beams {
          transform: rotateZ(-25deg);
          transition-duration: .15s;
        }
  
        & > .moon > circle {
          transition-delay: .25s;
          transition-duration: .5s;
        }
      }
    }
  }

  .check {
    position: absolute;
    display: block;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 6;
  }
  &:hover {
    .sun-beams {
      transform: rotate(45deg);
    }

  }
}

@media only screen and (max-width: 838px) {
  .ms_theme_mode {
      display: none;
  }
}

// Tooltip
.ms_tm--tooltip {
  position: absolute;
  background-color: var(--color-bg-darker);
  padding: 5px 8pt;
  right: 50%;
  opacity: 0;
  z-index: -1;
  font-size: 14px;
  border-radius: 4pt;
  white-space: nowrap;
  pointer-events: none;
  color: var(--color-contrast-higher);
  transition: right .3s var(--ease-elastic-4), opacity .3s var(--ease-elastic-4);

  &::before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    background: var(--color-bg-darker);
    top: 0;
    margin: auto;
    bottom: 0;
    left: 100%;
    transform: translate(-50%) rotate(45deg);
  }

}

.ms_theme_mode,
.back-to-top {
  &:hover {
    .ms_tm--tooltip {
      opacity: 1;
      right: calc(100% + 8pt);
    }
  }
}

@media (max-width: 992px) {
  .ms_theme_mode {
    right: calc(1.5rem/ 2);
    bottom: calc(1.5rem/ 2);
}
}
