.ms-tm {
    .ms-tm--title {
        display: block;
        color: var(--color-contrast-higher);
    }
    .ms-tm--function,
    .ms-tm--socials a {
        color: var(--color-contrast-higher);
    }
    .ms-tm--img {
        overflow: hidden;
        position: relative;
        z-index: 1;
    }
    img {
        max-width: 100%;
        width: 100%;

    }

}

.ms-tm.style-1 {

    padding-bottom: 4px;
    overflow: hidden;
    background: var(--color-primary);
    position: relative;

    .ms-tm--content {
        position: absolute;
        bottom: 0;
        padding: 20px;
        z-index: 2;
        width: 100%;
        
        
    }
    .ms-tm--title,
    .ms-tm--function {
        opacity: 0;
        transform: translateY(50%);
        transition: transform .5s var(--ease-in-out),opacity .5s var(--ease-in-out),-webkit-transform .5s var(--ease-in-out);
    }
    .ms-tm--socials {
        display: flex;
        position: absolute;
        right: 20px;
        bottom: 20px;
        z-index: 2;
        flex-direction: column;
        align-items: center;
    }
    .ms-tm--img {
        position: relative;
        z-index: 1;
        overflow: hidden;

        &::after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 44.5%, rgba(0, 0, 0, 0.13) 60.73%, rgba(0, 0, 0, 0.25) 80.85%, rgba(0, 0, 0, 0.5) 103.68%, #000000 120.75%);
            transition: opacity .5s var(--ease-in-out);
        }
    }
    .ms-tm--function {
        margin-top: 4pt;
    }
    .ms-s-icon{
        opacity: 0;
        transform: translateX(50%);
        transition: transform .3s var(--ease-in-out),opacity .3s var(--ease-in-out),-webkit-transform .3s var(--ease-in-out);
    }
    @for $i from 1 through 10 {
        .ms-s-icon:nth-child(#{$i}n) {
            transition-delay: #{$i * 0.1}s;
        }
    }
    .ms-s-icon {
        font-size: 20px;

        i {
            transition: transform 150ms var(--ease-in-out);
        }
        &:hover i {
            transform: translateY(-4px);
        }
    }
    .ms-s-icon + .ms-s-icon {
        padding-top: 4pt;
    }

    &:hover {
        .ms-tm--title,
        .ms-tm--function {
            opacity: 1;
            transform: translateX(0);
        }
        .ms-s-icon {
            opacity: 1;
            transform: translateX(0);
        }
        .ms-tm--img::after {
            opacity: 1;
        }
    }

}

.ms-tm.style-1.d_row .ms-tm--socials,
.ms-tm.style-3.d_row .ms-tm--socials {
    flex-direction: row;

    .ms-s-icon {
        transform: translateY(50%);
    }
    .ms-s-icon + .ms-s-icon {
        padding-left: 12pt;
    }
}

.ms-tm.style-3.d_row .ms-tm--socials {
    flex-direction: row;

    .ms-s-icon {
        transform: translateY(0);
    }

}

.ms-tm.style-1.d_row {

    &:hover {
        .ms-s-icon {
            transform: translateY(0);
        }
    }

}

.ms-tm.style-2 {

    .ms-tm--box {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        .ms-tm--img {
            width: 100%;
        }
        .ms-tm--content {
            display: inline-block;
            max-width: 100%;
            width: 100%;
            padding: 0 9.2%;

            .ms-tm--title {
                margin-top: 20px;
            }
            .ms-tm--function {
                margin-top: 4pt;
            }
            .ms-tm--desc {
                margin-top: 8pt;
            }
        }
        .ms-tm--socials {
            max-width: 82.6%;
            position: absolute;
            bottom: 9.2%;
            left: 9.2%;
            padding: 8pt 12pt;
            background-color: var(--color-bg);
            border-radius: 6pt;
            opacity: 0;
            transition: opacity 520ms cubic-bezier(.575,.015,0,.995);

            .ms-s-icon {
                font-size: 20px;
                i {
                    transition: transform 150ms var(--ease-in-out);
                }
                &:hover i {
                    transform: translateY(-4px);
                }
            }

        }

        &:hover .ms-tm--socials {
            opacity: 1;
        }
    }

}

.ms-tm.style-2.center {

    .ms-tm--box {
        flex-direction: column;
        text-align: center; 

        .ms-tm--content,
        .ms-tm--socials {
            max-width: 100%;
        }
        .ms-s-icon {
            padding: 0 4pt;
        }
    }

}

.ms-tm.style-2.left {
    .ms-s-icon:not(:first-child) {
        padding-left: 8pt;
        margin-left: 8pt;
        border-left: solid 1px var(--color-contrast-low);
    }
}
.ms-tm.style-2.right {

    .ms-tm--box {
        flex-direction: row-reverse;
        text-align: right; 

        .ms-s-icon:not(:first-child) {
            padding-left: 4pt;
        }
    }

}

.ms-tm.style-1,
.ms-tm.style-3 {
    .ms-tm--title,
    .ms-tm--function,
    .ms-tm--socials i {
        text-shadow: 1px 1px 2px rgba(0,0,0,.3); 
    }
}

.ms-tm.style-3 {

  .ms-tm--box {
        .ms-tm--img {
            position: relative;
            overflow: hidden;

            &::after {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                z-index: 2;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 44.5%, rgba(0, 0, 0, 0.13) 60.73%, rgba(0, 0, 0, 0.25) 80.85%, rgba(0, 0, 0, 0.6) 103.68%, #000000 120.75%);
                transition: opacity .5s var(--ease-in-out);
            }
        }
        .ms-tm--content {
            display: flex;
            width: 100%;
            padding: 20px;
            position: absolute;
            bottom: 0;
            z-index: 2;
            flex-direction: column;
        }
        .ms-tm--title,
        .ms-tm--function {
            max-width: 60%;
        }
        .ms-tm--function {
            margin-top: 4pt;
        }
        .ms-tm--socials {
            max-width: calc(40% - 15px);
            width: 100%;
            text-align: right;
            position: absolute;
            right: 20px;

            .ms-s-icon {
                font-size: 20px;

                i {
                    transition: transform 150ms var(--ease-in-out);
                }
                &:hover i {
                    transform: translateY(-4px);
                }
            }
        }
    }

}

.ms-tm.style-3.d_column .ms-tm--socials {
    display: flex;
    bottom: 20px;
    flex-direction: column;
}