.ms-bqt2 {
    margin-top: 20px;
    text-align: center;
    display: block;
    border: solid 3px var(--color-primary);
    border-radius: 12pt;
    padding-bottom: 20px;

    svg {
        transform: translateY(-50%);    
        font-size: var(--icon-xl);
        background-color: var(--color-bg);
        padding: 0 12pt;
        height: 40px;
        path {
            fill: var(--color-primary);
        }
    }
    p {
        margin: 0 0 20px;
        font-size: var(--text-md);
        line-height: var(--body-line-height);
        font-style: italic;
    }
    cite {
        font-size: 16px;
        font-weight: 400;
        margin-bottom: var(--space-sm);
    }
}
blockquote:not(.ms-bqt2) {
    position: relative;
    z-index: 2;
    position: relative;
    padding: 1.5rem;
    border: solid 2pt var(--color-primary); 
    border-radius: 6pt;

    p {
        position: relative;
        z-index: 3;
        margin-bottom: 0;
        font-size: 18px;
        font-style: italic;
    }

    &::before {
        content: '\201C';
        position: absolute;
        font-size: 64px;
        line-height: 1;
        top: 1.55rem;
        left: -0.75rem;
        z-index: 1;
        color: var(--color-primary);
        font-family: 'sans-serif';
    }

    &::after {
        content: '';
        position: absolute;
        top: 1.2rem;
        left: -3pt;
        height: 44px;
        width: 4pt;
        background-color: var(--color-bg);
    }

}

blockquote.has-text-align-right {
    padding: 1.5rem!important;
    border-left: solid 2pt var(--color-primary) !important;
    border-right: solid 2pt var(--color-primary) !important;
    &::before {
        content: '\201C';
        position: absolute;
        font-size: 64px;
        line-height: 1;
        top: 1.55rem;
        right: -0.75rem;
        z-index: 1;
        color: var(--color-primary);
        font-family: 'sans-serif';
    }

    &::after {
        content: '';
        position: absolute;
        top: 1.2rem;
        left: auto;
        right: -3pt;
        height: 44px;
        width: 4pt;
        background-color: var(--color-bg);
    }
}

blockquote.has-text-color {
    &::before {
        content: none;
    }
}
.wp-block-pullquote blockquote::before {
    content: none;
}