.portfolio-feed.ms-p--l {
    overflow: inherit !important;
    .ms-p-list {
        display: flex;
        flex-direction: column;
    }
    .ms-p-list__item {
        display: inline-flex;
        transition: opacity .6s cubic-bezier(.19,1,.22,1);

        a {
            display: flex;
        }
        .p-list-c {
            color: var(--color-contrast-medium);
            margin-top: 4pt;
            margin-right: 8pt;
        }
        
        h3 {
            font-size: 52px;
            text-transform: uppercase;
            font-weight: 800;
            line-height: 1;
            margin-bottom: 16pt;
            margin-right: 8pt;
        }
        .p-list-item__svg {
            display: flex;
            width: 24px;
            height: 24px;
            opacity: 0;
            transform: rotate(-45deg) scale(.5);
            transition: opacity .6s cubic-bezier(.19,1,.22,1),transform .6s cubic-bezier(.19,1,.22,1);
            svg {
                fill: var(--color-primary);
                -webkit-animation: rotateAnim 10s linear infinite;
                animation: rotateAnim 10s linear infinite;
            }
        }
        &:hover .p-list-item__svg {
            opacity: 1;
            transform: rotate(0) scale(1);
        }
        @keyframes rotateAnim { 
            100% { 
                -webkit-transform: rotate(360deg); 
                transform:rotate(360deg); 
            } 
        }
    }
    .ms-p-list__aside {

        .ms-p-list__aside-inner{
            position: sticky;
            position: -webkit-sticky;
            top: 8vw;
            height: calc((9 / 16) * 80%);

            .ms-p-list__aside-wrap {

                .ms-p-list__img {
                    position: absolute;
                    top:0;
                    left: 0;
                    width: 100%;
                    height: auto;
                    overflow: hidden;
                    border-radius: 8pt;
                    
                    img {
                        transform: translateY(101%);
                        transition: transform .6s cubic-bezier(1,0,.25,.995);
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        border-radius: 8pt;
                        will-change: transform;
                        backface-visibility: hidden;
                    }
                }
                .ms-p-list__img.active {
                    opacity: 1;
                    img {
                        z-index: 1;
                        transform: translateZ(0);
                        transition-duration: 1s;
                        transition-timing-function: cubic-bezier(.19,1,.22,1);
                    }
                }

            }

        }
        
    }

}

@media (max-width: 1024px) {
    .ms-p-list__aside {
        display: none;
    }
    .portfolio-feed.ms-p--l .ms-p-list__item .p-list-item__svg {
        width: 18px;
        height: 18px;
    }
}